import React, { Component } from 'react';
import TeachersListDisplay from './TeachersListDisplay';

class TeachersListContainer extends Component {

  state = {
    columns: [
      { title: 'Nombre', dataIndex: 'name', key: 'name' },
      { title: 'Email', dataIndex: 'email', key: 'email' },
      { title: 'Cursos', dataIndex: 'cursos', key: 'cursos' },
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.editTeacher(obj._id)}>Edit</button>)},
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.props.removeTeacher(obj._id)}>Delete</button>)}
    ]
  }

  render() {
    const {columns} = this.state
    const {data} = this.props
    console.log(this.state.data)
    return (
      <div>
        <TeachersListDisplay columns={columns} data={data} />
      </div>
    );
  }
}

export default TeachersListContainer;