import React from 'react'
import {Input, Button} from 'antd'

const SignupDisplay = ({createUser, loading, signup, onChange }) =>{
  return(
    <div style={{width:600, minHeight:'100vh', margin:"0 auto", padding:80}}>
        <form onSubmit={createUser} style={{padding:20}}>
        <h1 className='title'>Registra un Campus</h1>
        <p>
            <Input 
                name="username"
                type="text"
                onChange={onChange}
                value={signup.username}
                placeholder="Tu nombre de usuario" 
            /> 
        </p>
        <p>
            <Input 
                name="name"
                type="text"
                onChange={onChange}
                value={signup.name}
                placeholder="El nombre de tu escuela" 
            />
            
        </p>
        <p>
        <Input 
            name="email"
            type="email"
            onChange={onChange}
            value={signup.email}
            placeholder="Tu correo" 
            />    
        </p> 
        <p>
        <Input 
            name="password"
            type="password"
            onChange={onChange}
            value={signup.password}
            placeholder="Tu Password" 
            />    
        </p>  
        <p>
        <Input 
            name="password2"
            type="password"
            onChange={onChange}
            value={signup.password2}
            placeholder="Repite tu Password" 
            />    
        </p>   
        <Button loading={loading} type="primary" htmlType="submit" >Registrar Escuela</Button>
        </form>
    </div>
  )
}

export default SignupDisplay