import React from 'react'
import { Card, Icon } from 'antd';

const { Meta } = Card;


const CoursesListDisplay = ({cursos}) =>{

  return(
    <div style = {{display:'flex', flexWrap:'wrap'}}>
     {cursos.map((c,i) => ( 
     <Card 
     style={{ width:'28%',height:'100%', margin:'1%'}} 
     key={i} 
     cover={<img style={{height:'170px'}} 
     alt="example" 
     src={c.photoURL} />} 
     actions={[<Icon type="edit" />,
      <Icon type="ellipsis" />]}> 
      <Meta title={c.name} description={c.profesor}/></Card>))} 
    </div>
)}

export default CoursesListDisplay