import React, { Component } from 'react';
import AddClassroomDisplay from './AddClassroomDisplay'

class AddClassroomContainer extends Component{

    state = {
        correos:{},
        classname:{},
        loading:false
    }

    render(){
        const {loading} = this.state
        const {createStudent, onChange} = this.props
        return(
            <AddClassroomDisplay
            createStudent = {createStudent}
            loading = {loading}
            onChange = {onChange}
            />
        )
    }
}

export default AddClassroomContainer