import React, { Component } from 'react';
import ClassroomListDisplay from './ClassroomListDisplay';


class ClassroomListContainer extends Component {

  state = {   
    columns: [
      { title: 'Name', dataIndex: 'name', key: 'name' },
      { title: 'Students', dataIndex: 'students.length', key: 'students' },
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.props.editClass(obj._id)}>Editar</button>)},
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.props.removeClass(obj._id)}>Borrar</button>)}
    ]
  }


  render() {
    const {columns} = this.state
    const {data} = this.props
    console.log(this.state.data)
    return (
      <div>
        <ClassroomListDisplay columns={columns} data={data} />
      </div>
    );
  }
}

export default ClassroomListContainer;
