import React from 'react'
import {Input, Button} from 'antd'

const AddTeachersDisplay = ({createTeacher, loading, onChange, signupTeacher }) =>{
  return(
    <div>
      <form  onSubmit={createTeacher} style={{padding:20}}>
        <h2>Agregar Profesores</h2>
         <p>
          <Input 
              name="name"
              type="text"
              onChange={onChange}
              value={signupTeacher.name}
              placeholder="Nombre completo del profesor" 
          />   
      </p>
      <p>
      <Input 
          name="username"
          type="username"
          onChange={onChange}
          value={signupTeacher.username}
          placeholder="Username" 
          />    
      </p> 
      <p>
      <Input 
          name="email"
          type="email"
          onChange={onChange}
          value={signupTeacher.email}
          placeholder="Correo del profesor" 
          />    
      </p> 
       <p>
        <Button loading={loading} type="primary" htmlType="submit" >Agregar</Button>
        </p> 
      </form>
    </div> 
  )
}

export default AddTeachersDisplay