import React, {Component} from 'react'
import AddCoursesDisplay from './AddCoursesDisplay';
import axios from 'axios'
import toastr from 'toastr'

class AddCoursesContainer extends Component{
  
state = {
  teachers :[],
  classrooms : [],
  course:{}
}

  onChange = (e) => {
    console.log(e.target)
    const field = e.target.name
    const value = e.target.value
    const {course} = this.state
    course[field] = value
    console.log(course)
    this.setState({course})
  }

  upload = (e) => {
    const field = 'photoURL'
    console.log(e.target.files[0])
    const value = e.target.files[0]
    const {course} = this.state
    course[field] = value
    this.setState({course})
  }
  
  componentWillMount() {

    axios.get('https://eci-campus-app.herokuapp.com/admin/teachersSelect',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res => {
        this.setState({
          teachers: res.data})
      }).catch(e=>{
        console.log(e)
      })

      axios.get('https://eci-campus-app.herokuapp.com/admin/classroomsSelect',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res => {
        this.setState({
          classrooms: res.data})
      }).catch(e=>{
        console.log(e)
      })
  }

  createCourse = (e) => {
    e.preventDefault()
    const {course} = this.state
    const form = new FormData()
    for(let key in course){
    form.append(key,course[key])
    }    
    axios.post('https://eci-campus-app.herokuapp.com/admin/addCourses', form,{
      headers:{
        'Authorization':localStorage.getItem('token')            
      }
    })
    .then(user=>{
        console.log(user)
        toastr.success("Exito! Agregaste un curso")
    })
    .catch(e=>toastr.error("Error"))
}


  render(){
    const {teachers, classrooms, course} = this.state
    console.log(this.state)
    return(
      <AddCoursesDisplay
      createCourse={this.createCourse}
      teachers = {teachers}
      classrooms = {classrooms}
      course={course}
      onChange={this.onChange}
      upload={this.upload}
      />
    )
  }
}

export default AddCoursesContainer