import React, { Component } from 'react';
import AddClassroomContainer from './AddClassroom/AddClassroomContainer';
import ClassroomListContainer from './ClassroomList/ClassroomListContainer';
import axios from 'axios'
import toastr from'toastr'

class CreateClassroom extends Component {

  state={
    correos:{},
    classname:{},
    loading:false,
    data:[]
  }

componentWillMount(){
  axios.get('https://eci-campus-app.herokuapp.com/admin/manageClassroom',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    })
      .then(res => {        
        this.setState({
          data: res.data})
      })
}

removeClass=(_id)=>{    
  axios.post('https://eci-campus-app.herokuapp.com/admin/deleteClassroom',{_id},{
    headers:{
      'Authorization':localStorage.getItem('token')
    }
  }).then(res =>{
    console.log('se borro!')
    let {data} = this.state
    data = data.filter(d=>d._id!==_id)
    this.setState({data})
  })
  .catch(e=>{
    console.log(e)
  })
}

onChange = (e) => {
  const value = e.target.value
  const key = e.target.name 
  const {correos} = this.state
  correos[key]=value
  console.log(correos)
  this.setState({correos})
}

createStudent = (e) => {
  e.preventDefault()
  const {correos} = this.state
  axios.post('https://eci-campus-app.herokuapp.com/admin/manageClassroom', correos,{
    headers:{
      'Authorization':localStorage.getItem('token')            
    }
  })
  .then(user=>{
      console.log(user.data)
      const {data} = this.state
      data.unshift(user.data)
      this.setState({data})
      toastr.success("Exito! Se envio un correo a los estudiantes")
  })
  .catch(e=>toastr.error("Error"))
}

  render() {
    const {data} = this.state
    return (
      <div style={{ width:'60%', margin : '0 auto',paddingTop:100, height:'100vh'}}>
        <div style={{ display:'flex'}}>
        <div className='title' style={{width:'30%'}}>
          <h1>Crear Salones</h1>
          <p>Administra aqu√≠ los salones. Crear, Editar salones completos. Dar de alta a alumnos.
          </p>
        </div>
        <div style={{width:'70%', marginLeft:'40px'}}><AddClassroomContainer createStudent={this.createStudent} onChange={this.onChange}/></div>
        </div >
        <ClassroomListContainer data={data} removeClass={this.removeClass}/>
      </div>
    );
  }
}

export default CreateClassroom;