import React, { Component } from 'react';
import CoursesListDisplay from './CoursesListDisplay'
import axios from 'axios'

class CoursesListContainer extends Component {

  state = {
    cursos: [],
  }

  componentDidMount() {

    axios.get('https://eci-campus-app.herokuapp.com/admin/getallcourses',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    })
      .then(res => {
        this.setState({
          cursos: res.data})
      })
  }

  render() {
    const {cursos} = this.state
    return (
      <div className='title'>
        <h1>Lista de Cursos</h1>
        <CoursesListDisplay cursos={cursos}/>
      </div>
    );
  }
}

export default CoursesListContainer;
