import React, { Component } from 'react';
import AddTeachersDisplay from './AddTeachersDisplay'

class AddTeachersContainer extends Component{

    state = {
        loading:false
    } 

    render(){
        const {loading} = this.state
        const {createTeacher, onChange, signupTeacher} = this.props

        return(
            <AddTeachersDisplay
            createTeacher = {createTeacher}
            loading = {loading}
            onChange = {onChange}
            signupTeacher = {signupTeacher}
            />
        )
    }
}

export default AddTeachersContainer