import React from 'react'
import {Input, Button} from 'antd'

const { TextArea } = Input;

const AddClassroomDisplay = ({createStudent, loading, onChange }) =>{
  return(
    <div>
      <form  onSubmit={createStudent} style={{width:'100%', padding:20}}>
        <h2>Crear classroom</h2>
        <Input style={{marginBottom:10}} name="name" placeholder="Classroom name"  onChange={onChange}/>
        <p>
        <TextArea
            name="email"
            type="email"
            onChange={onChange}
            placeholder="Correos separados por ',' ejemplo: algo@algo.com,otro@otro.com" 
            autosize={{ minRows:4, maxRows: 8 }}
            />    
        </p> 
        <p>
        <Button loading={loading} type="primary" htmlType="submit" >Agregar Estudiantes</Button>
        </p> 
      </form>
    </div> 
  )
}

export default AddClassroomDisplay