import React from 'react'
import {Upload, message, Icon, Input, Button } from 'antd';


const Dragger = Upload.Dragger;

const props = {
  name: 'file',
  action: '//jsonplaceholder.typicode.com/posts/',
  headers: {
    authorization: 'authorization-text',
  },
  onChangeUpload(info) {
    if (info.file.status !== 'uploading') {
      console.log(info.file, info.fileList);
    }
    if (info.file.status === 'done') {
      message.success(`${info.file.name} file uploaded successfully`);
    } else if (info.file.status === 'error') {
      message.error(`${info.file.name} file upload failed.`);
    }
  },
};

const AddCoursesDisplay = ({loading, course,upload,createCourse, onChange, teachers=[], classrooms=[]}) =>{
  return(
    <div>
     <h2>Crear Curso</h2>
      <form onSubmit={createCourse}>
        <div style={{display:'flex'}}>
          <div style={{width:'50%', marginRight:40}}>
            <p>
              <Input 
                name="name"
                type="text"
                placeholder="Nombre del curso"
                value={course.name} 
                onChange={onChange}
              /> 
            </p>
            <p style={{display:'flex'}}>
              <h3 style={{marginRight:10}}>Fecha de Inicio : </h3> 
              <Input 
                style={{width:'50%'}}
                name="date"
                type="date"
                placeholder="Fecha de Inicio" 
                value={course.date} 
                onChange={onChange}
              /> 
            </p>
            <p>
              <Input 
                name="horario"
                type="text"
                placeholder="Horarios. ejemplo : JUEVES DE 16:00-20:00 HORAS" 
                value={course.horario} 
                onChange={onChange}
              />    
            </p> 
            <p style={{display:'flex'}}>
            <h3 style={{marginRight:10}}>Profesor que imparte : </h3>
              <select onChange={onChange} name='profesor' value={course.profesor}>
              {teachers.map((t,i)=> <option  key={i} value={t.name}>{t.name}</option> )} 
              </select>
            </p>
            <p>
            <h3 style={{marginRight:10}}>Asignar Grupo : </h3> 
              <select
                style={{ width: '100%' }}
                onChange={onChange}
                name='classroom'
                value={course.classroom}
              >
              {classrooms.map((c,i)=> <option  key={i} value={c.name}>{c.name}</option> )}
              </select>
            </p>
          </div>
          <div>
            <p>
              <h3>Agrega una imagen del curso : </h3>
              <Input type="file" onChange={upload}/>
            </p>
            <p>
            <h3>Subir Material del curso : </h3>
              <Dragger {...props}>
            <p className="ant-upload-drag-icon">
              <Icon type="inbox" />
            </p>
              <p className="ant-upload-text">Da click o arrastra los archivos para subirlos</p>
              <p className="ant-upload-hint">Manuales del Curso</p>
              </Dragger>
            </p> 
          </div>
        </div>
      <Button loading={loading} type="primary" htmlType="submit" >Crear Curso</Button>
    </form>
  </div> 
  )
}

export default AddCoursesDisplay