import React from 'react'
import {Switch, Route} from 'react-router-dom'
import ManageClassroom from './ManageClassroom'
import ManageStudents from './ManageStudents'
import ManageTeachers from './ManageTeachers'
import AddCourses from './AddCourses'
import ManageCalendar from './ManageCalendar'

const AdminRoutes = () => {
    return(
        <Switch>
            <Route path="/admin/manageClassroom" component={ManageClassroom}/>
            <Route path="/admin/manageStudents" component={ManageStudents}/>
            <Route path="/admin/manageTeachers" component={ManageTeachers}/>
            <Route path="/admin/addCourses" component={AddCourses}/>
            <Route path="/admin/manageCalendar" component={ManageCalendar}/>
        </Switch>
    )
}

export default AdminRoutes
