import React, { Component } from 'react';
import AddTeacherContainer from './manageTeachers/AddTeachers/AddTeacherContainer'
import TeachersListContainer from './manageTeachers/TeachersList/TeachersListContainer'
import toastr from 'toastr'
import axios from 'axios'

class ManageTeachers extends Component {

  state={
    signupTeacher:{},
    data:[]
  }

  componentWillMount() {

    axios.get('https://eci-campus-app.herokuapp.com/admin/manageTeachers',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    })
      .then(res => {
        this.setState({
          data: res.data})
      })
  }

  removeTeacher=(_id)=>{    
    axios.post('https://eci-campus-app.herokuapp.com/admin/deleteTeacher',{_id},{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res =>{
      console.log('se borro!')
      let {data} = this.state
      data = data.filter(d=>d._id!==_id)
      this.setState({data})
    })
    .catch(e=>{
      console.log(e)
    })
  }

  onChange = (e) => {
    const field = e.target.name
    const value = e.target.value
    const {signupTeacher} = this.state
    signupTeacher[field] = value
    this.setState({signupTeacher})
}

  createTeacher = (e) => {
    e.preventDefault()
    const {signupTeacher} = this.state
    axios.post('https://eci-campus-app.herokuapp.com/admin/manageTeachers', signupTeacher,{
      headers:{
        'Authorization':localStorage.getItem('token')            
      }
    })
    .then(user=>{
      console.log(user.data)
      const {data} = this.state
      data.unshift(user.data)
      this.setState({data})
        toastr.success("Agregaste un profesor!")
    }).catch(e=>toastr.error("Error"))
}


  render() {
    const {signupTeacher, data} = this.state
    return (
      <div style={{ width:'60%', margin : '0 auto',paddingTop:100, height:'100vh'}}>
        <div style={{ display:'flex'}}>
          <div className='title' style={{width:'30%'}}>
            <h1>Administrar Profesores</h1>
            <p>Edita, da de alta o de baja a los profesores.</p>
          </div>
          <div style={{width:'70%', marginLeft:'40px'}}><AddTeacherContainer signupTeacher={signupTeacher} createTeacher={this.createTeacher} onChange={this.onChange}/></div>
        </div>
        <TeachersListContainer data={data} removeTeacher={this.removeTeacher}/>
      </div>
    );
  }
}

export default ManageTeachers;